@import url('https://fonts.googleapis.com/css2?family=Anek+Latin:wght@100;200&family=Anek+Malayalam:wght@100;200&family=Roboto+Condensed:wght@300&family=Roboto+Mono:wght@100&family=Shrikhand&display=swap');

.Container-inicial{

    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.container-header{

    width: 100%;
    height: 75px;
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,26,121,1) 35%, rgba(3,10,70,1) 100%);
    color:white;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-around;
    padding: 20px;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: bolder;
}

.container-section, .container-section2{

    width: 98%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 45px;
    margin-top: 50px;
}

.container-cadastro{

    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 50px;
    margin-top: 50px;
    border: 0.7px solid rgba(17, 17, 17, 0.214);
    padding: 20px;
}

.menu{

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
    flex-direction: row;
}

.logo{

    font-size: 0.9rem;
}

.menu li{

    font-size: 1rem;
    font-weight: bolder;
    cursor: pointer;
    border-right: 0.6px solid rgba(255, 255, 255, 0.366);
    padding: 5px;
    height: 20px;
}

.liMenu{

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.liMenu a:hover{

    color: yellow;
}

.IconLogo{

    width: 25px;
}

.receberItem, .receberItem2{

    width: 95%;
    height: 350px;
    margin-top: 5px;
    overflow-x: hidden;
    display: flex;
    align-items: center;
    gap: 30px;
    flex-direction: column;
    font-weight: normal;
    padding: 10px;
}

.delete{

    width: 200px;
    height: 35px;
    background-color: red;
    color:white;
}

.liItem{

    width: 65%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    justify-content: space-between;
    font-size: 0.8rem;
    border-bottom: 0.8px solid rgba(0, 0, 0, 0.167);
    padding-left: 30px;
    font-weight: bolder;
    text-transform: uppercase;
}

.liItem:nth-child(1){

    display: none;
}

.pp{

    width: 900px;
}
.liItem:hover{

    background-color: rgba(4, 4, 98, 0.807);
    color:white;
    font-size: bolder;
    cursor: pointer;
    transition: 0.8s;
    border-radius: 4px;
}

.logoAlte{

    color: rgba(255, 205, 3, 0.99);   
}

.boxInputs, .boxInputs2{

    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin-top: -20px;
}

.boxInputs3{

    width: 100%;
    height: 390px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 70px;
}

.InputsDate{

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 20px;
}

.InputAdd{

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 10px;
}

.inputGlob{

    width: 350px;
    height: 50px;
    border: 0.6px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    outline: none;
    text-align: center;
    font-size: 1rem;
}

.buttonGlob{

    width: 150px;
    height: 50px;
    border: none;
    background-color: rgb(8, 8, 137);
    color:white;
    font-size: 1.2rem;
    border-radius: 3px;
    cursor: pointer;
}

.buttonGlob:hover{

    background-color: rgb(11, 11, 193);

}

.inputData{

    width: 148px;
    height: 50px;
    text-align: center;
    font-size: 1.1rem;
    padding: 10px;
    cursor: pointer;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row-reverse;
}

.IconLixo{

    width: 30px;
    cursor: pointer;
}

.IconG{

    width: 20px;
}

.buttonDele{    
    
    width: 40px;
    background-color: white;
    border: none;
}

.buttonDele:hover{

    background-color: red;
}

.receberData{

    width: 150px;
    height: 45px;
    text-align: center;
    line-height: 45px;
}

.divi, .divi2{

    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.textLista{

    font-size: 0.8rem;
}

.container-rodape{

    width: 100%;
    height: 100vh;
    background-color: rgb(4, 4, 64);
    line-height: 70px;
    margin-top: 30px;
    display: flex;
    align-items: center;
    flex-direction: column;
    
}

.text1{

    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    gap: 6px;
    flex-direction: row;
    font-size: 0.9rem;
    font-weight: normal;
    color:white;

}

.copitext{

    color: rgb(75, 75, 75);
    font-weight: bolder;
}

.copiar,.pdf{

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.copiar img, .pdf img{

    width: 35px;
    cursor: pointer;
}

.pdf{

    margin-left: 5px;
}

.copiar p, .pdf p{

    font-size: 0.8rem;
    cursor: pointer;
}

.copiar img:active{

    padding: 2px;
}

.boxdata{

    width: 100px;
    text-align: center;
    border-bottom: 0.6px solid rgba(255, 255, 255, 0.366);
}