*{

  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

a{

  color: white;
}

body{

  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bolder;
  background-color: rgba(204, 204, 204, 0);
  overflow-y: hidden;
}

::-webkit-scrollbar {
  width: 15px;               
}

::-webkit-scrollbar-track {
  background: white;  
       
}

::-webkit-scrollbar-thumb {
  border-radius: 20px;       
  border: 0.6px solid rgba(0, 0, 0, 0.444);  
}

